.Apropos {
    overflow: hidden;
    padding: 0;
    margin: 0;
    .contenu {
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        margin: 0 10% 0 10%;
        .gauche {
            display: flex;
            align-items: center;
            width: 50%;
            height: 35%;
            background-color: rgb(155, 47, 110);
            font-size: 1.7em;
            text-align: center;
            color: white;
        }
        .droite {
            display: flex;
            align-items: center;
            width: 50%;
            height: 35%;
            background-color: rgb(155, 47, 110);
            align-self: flex-end;
            font-size: 1.7em;
            text-align: center;
            color: white;
        }
    }

    @media only screen and (max-width: 700px) {
        .contenu {
            .gauche {
                width: 80%;
                font-size: 1.3em;
            }
            .droite {
                width: 80%;
                font-size: 1.3em;
            }

        }
    }
}